"use client";

import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabase";
import PropertyCalendar from "./PropertyCalendar";
import LeadsTab from "./LeadsTab";

// üîí TEMP OWNER LOCK ‚Äî CHANGE LATER
const OWNER_ID = "3156a3d2-5abb-45c2-8953-d7ac3f89a408";

export default function OwnerDashboard() {
  const [tab, setTab] = useState<"leads" | "properties">("leads");

  const [properties, setProperties] = useState<any[]>([]);
  const [propertyId, setPropertyId] = useState("");

  const [rooms, setRooms] = useState<any[]>([]);
  const [roomName, setRoomName] = useState("");
  const [roomPrice, setRoomPrice] = useState("");

  useEffect(() => {
    fetchProperties();
  }, []);

  useEffect(() => {
    if (propertyId) fetchRooms(propertyId);
  }, [propertyId]);

  const fetchProperties = async () => {
    const { data } = await supabase
      .from("properties")
      .select("*")
      .eq("owner_id", OWNER_ID);

    setProperties(data || []);
  };

  const fetchRooms = async (pid: string) => {
    const { data } = await supabase
      .from("rooms")
      .select("*")
      .eq("property_id", pid)
      .order("created_at");

    setRooms(data || []);
  };

  const addRoom = async () => {
    if (!roomName || !roomPrice || !propertyId) {
      alert("Fill all fields");
      return;
    }

    await supabase.from("rooms").insert({
      property_id: propertyId,
      name: roomName,
      price: Number(roomPrice),
      is_active: true,
    });

    setRoomName("");
    setRoomPrice("");
    fetchRooms(propertyId);
  };

  return (
    <main className="p-10 max-w-5xl space-y-6">
      <h1 className="text-2xl font-bold">Owner Dashboard</h1>

      {/* TABS */}
      <div className="flex gap-4">
        <button
          onClick={() => setTab("leads")}
          className={`px-4 py-2 rounded ${
            tab === "leads" ? "bg-black text-white" : "border"
          }`}
        >
          Leads
        </button>
        <button
          onClick={() => setTab("properties")}
          className={`px-4 py-2 rounded ${
            tab === "properties" ? "bg-black text-white" : "border"
          }`}
        >
          Properties
        </button>
      </div>

      {/* LEADS */}
      {tab === "leads" && <LeadsTab ownerId={OWNER_ID} />}

      {/* PROPERTIES */}
      {tab === "properties" && (
        <>
          <select
            className="border p-2 w-full bg-white text-black"
            value={propertyId}
            onChange={(e) => setPropertyId(e.target.value)}
          >
            <option value="">Select Property</option>
            {properties.map((p) => (
              <option key={p.id} value={p.id}>
                {p.name}
              </option>
            ))}
          </select>
           <span
  className={`px-2 py-1 rounded text-xs font-medium ${
    l.status === "booked"
      ? "bg-green-600 text-white"
      : l.status === "viewed"
      ? "bg-blue-600 text-white"
      : l.status === "shortlisted"
      ? "bg-orange-500 text-white"
      : l.status === "dropped"
      ? "bg-red-600 text-white"
      : "bg-gray-500 text-white"
  }`}
>
  {l.status}
</span>

          {propertyId && (
            <>
              <div className="border rounded p-6">
                <PropertyCalendar propertyId={propertyId} />
              </div>

              <div className="border rounded p-6 space-y-4">
                <h2 className="font-semibold">Rooms</h2>

                <div className="flex gap-2">
                  <input
                    className="border p-2 flex-1 bg-white text-black"
                    placeholder="Room name"
                    value={roomName}
                    onChange={(e) => setRoomName(e.target.value)}
                  />
                  <input
                    className="border p-2 w-32 bg-white text-black"
                    placeholder="Price"
                    value={roomPrice}
                    onChange={(e) => setRoomPrice(e.target.value)}
                  />
                  <button
                    onClick={addRoom}
                    className="bg-black text-white px-4"
                  >
                    Add
                  </button>
                </div>

                {rooms.map((r) => (
                  <div key={r.id} className="border p-2 rounded">
                    {r.name} ‚Äì ‚Çπ{r.price}
                  </div>
                ))}
              </div>
            </>
          )}
        </>
      )}
    </main>
  );
}
